import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { Webfields } from './webfields.model';
import { Headers } from '@angular/http';
import 'rxjs/Rx';
import { Response } from '@angular/http';
@Injectable()
export class WebsiteService {

  constructor(private http:Http) { }

  getfields(){
    return this.http.get('https://dataservice-ebe36.firebaseio.com/fields.json').map((response:Response) =>{ const dat = response.json(); return dat  });
  }

  setfields(fields:Webfields){
    const body = JSON.stringify(fields);
    const headers = new Headers({'Content-Type':'application/json'});
    return this.http.put('https://dataservice-ebe36.firebaseio.com/fields.json',body,{headers:headers}).map((response:Response) =>{ const dat = response.json(); return dat  });
  }

}



