import { Component, OnInit } from '@angular/core';
import { Webfields } from '../webfields.model';
import { WebsiteService } from '../website.service';
import { ViewChild } from '@angular/core';
import { ElementRef } from '@angular/core';
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  webtext:Webfields= new Webfields('slidertextone','slidertexttwo','slidertextthree','sliderparaone','sliderparatwo','sliderparathree','sliderimageurlone','sliderimageurltwo','sliderimageurlthree','foodimageone','foodimagetwo','foodimagethree','heading','paraone','paratwo','subheading','para','contend','contendheadone','contendone','contendheadtwo','contendtwo','contendheadthree','contendthree','asd');
  display:boolean=true;
  postop:number=0;
  @ViewChild('elementedit') elem:ElementRef;
  constructor(private serv:WebsiteService) { }

  ngOnInit() {
    this.serv.getfields().subscribe(
      (data:Webfields) =>{ this.webtext = data }

    )

  }

  updatefields(webtext:Webfields){
    this.serv.setfields(webtext).subscribe(
      (data:Webfields) => {alert("updated")}
    );
  }

  togledisplay(){
    this.display = !this.display;
  }

}



